body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

div.f-ele-wrapper {
  width: calc(16.66% - 24px);
  display: inline-block;
  margin: 6px 12px;
}

div.f-ele {
  border: 1px solid #eee;
  border-radius: 4px;
  padding: 12px;
  cursor: pointer;
}

p.f-ele-preview {
  background: url('./folder-icon.png') no-repeat;
  background-position: center;
  background-size: contain;
  width: 100%;
  height: 90px;
  margin: 0;
}

div.f-ele-footer {
  margin: 6px auto 0 auto;
  text-align: center;
  width: 80%;
}

div.f-ele-selected {
  background: #c5edfe;
}

div.f-panel {
  margin: 12px 16px;
  padding: 6px 12px;
}
div.f-head-path {
  
  top: 40px;
  color: #888;
}

div.f-head-wrapper {
  position: relative;
  height: 70px;
  margin: 12px;
}

div.btn-default.f-btn {
  border: 1px solid #ccc;
}

div.input-group.f-head-search {
  position: absolute;
  width: 60%;
  right: 0;
}

div.f-head-btns {
  position: absolute;
  left: 0;
}

.head-path-btn {
  cursor: pointer;
  padding: 0 .5rem 3px .5rem;
  font-size: .875rem;
  line-height: 1;
  font-weight: 400;
  color: #007bff;
  background-color: transparent;
  display: inline-block;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: 1px solid transparent;
  border-radius: .2rem;
}

.head-path-btn-first {
  padding-left: 0!important;
}


div.f-hide-if-sm {
  display: inherit;
}

@media screen and (max-width: 800px) {
  div.f-ele-wrapper {
      width: calc(20% - 24px);
  }
}

@media screen and (max-width: 400px) {
  div.f-ele-wrapper {
      width: calc(50% - 24px);
  }
  div.input-group.f-head-search {
      position: absolute;
      right: 0;
  }    
  div.f-hide-if-sm {
      display: none;
  }
}

/* Customize the label (the container) */
.check-wrapper {
  display: block;
  position: relative;
  padding-left: 10px;
  cursor: pointer;
  font-size: 16px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.check-text {
  width: calc(100% - 20px);
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow-x: auto;
  display: inline-block;
}

/* Hide the browser's default checkbox */

.check-wrapper input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom checkbox */

.checkmark {
  position: absolute;
  top: 4px;
  left: 0;
  height: 16px;
  width: 16px;
  background-color: #eee;
}

/* On mouse-over, add a grey background color */

.check-wrapper:hover input~.checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */

.check-wrapper input:checked~.checkmark {
  background-color: #2196F3;
}

/* Create the checkmark/indicator (hidden when not checked) */

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */

.check-wrapper input:checked~.checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */

.check-wrapper .checkmark:after {
  top: 2px;
  left: 6px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.pagination {
  margin-top: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: end;
      justify-content: flex-end;
}


.pagination span {
  color: #888;
  float: left;
  padding: 2px 8px;
  text-decoration: none;
  -webkit-transition: background-color .3s;
  -o-transition: background-color .3s;
  transition: background-color .3s;
  cursor: pointer;
}

.pagination span.active {
  background-color: #2196F3;
  color: white;
}

.pagination span:hover:not(.active) {background-color: #ddd;}

.fileInfoList {
  list-style: none;
}

.pdf-btn-wrap .pdf-next-prev {
	text-align: left;
}

.pdf-btn-wrap .pdf-next-prev,
.pdf-btn-wrap .pdf-page,
.pdf-btn-wrap .pdf-zoom,
.pdf-btn-wrap .pdf-download-wrap {
	float: left;
	width: 25%;
}

[class^="pdf-"], [class*=" pdf-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'pdf-icons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.pdf-btn-wrap .pdf-download-wrap {
	text-align: right;
}

.react-layout-components--box {
  display: -ms-flexbox;
  display: flex;
}


